.footer {
  padding: 100px 0;
  overflow: hidden;
}

.footer .social {
  display: flex;
  flex-direction: row;
  gap: 20px;
}

.footer .social_link {
  width: 35px;
}

.footer__contact {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  line-height: 1.44;
  font-weight: 400;
}

.footer__contact h3 {
  font-weight: 600;
  color: #52575C;
  font-size: 18px;
  margin-bottom: 10px;
}

.footer__contact a {
  font-size: 18px;
  color: #141718;
  font-weight: 400;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.footer__contact a:hover {
  color: #d41a3b;
  transform: translateX(7px);
}

.footer_top {
  width: 30%;
}

.footer_bot {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  gap: 30px;
}
